import {lazy, useEffect, useState} from "react";
import config from "../config.js";
import styled from "styled-components";
import classNames from "../any/classNames.js";
import {getLastVersion} from "../any/versions.jsx";
import HolidaysTimerLogistics from "../components/HolidaysTimerLogistics.jsx";
import ContentViewer from "../components/layout/ContentViewer.jsx";

const Header = lazy(() => import("../components/Header"));
export default function UsefulLinksScreen() {
    const [list, setList] = useState([]);

    useEffect(() => {
        async function getData() {
            try {
                const request = await fetch(`/data/links-data.json?v=${getLastVersion()}`);
                const response = await request.json();
                setList(response);
            } catch (error) {
                console.log('error')
            } finally {
                //
            }
        }

        getData();

    }, [])

    function openLink(url) {
        const link = document.createElement('a');
        link.href = url;
        link.target = '_blank';
        link.click();
        link.remove();
    }

    return (
        <ContentViewer title={'Полезные ссылки'}>
            <main className="links">
                <div className="links-list">
                    {list.length > 0 && list.map((item, index) => (
                        <LinkCard className={classNames((item?.variant === 'h' && 'horizontal'))} key={index}
                                  onClick={() => openLink(item.link)}>
                            <div className="image">
                                <img src={item.image} alt="prev"/>
                            </div>
                            <div className={"title"}>
                                <h4>{item.title}</h4>
                            </div>
                        </LinkCard>
                    ))}
                    {list.length === 0 && (
                        <p>Список пуст</p>
                    )}
                </div>
            </main>
        </ContentViewer>
    )
}
const LinkCard = styled.div`
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: calc(100% / 4 - 2em);
    gap: 10px;
    box-shadow: 3px 3px 5px rgba(23, 23, 23, 0.1);
    background-color: #FFFFFF;
    cursor: pointer;
    border-radius: 6px;
    transition: all 0.3s ease;
    padding: 20px 0;
    border: 1px solid rgba(23, 23, 23, 0.1);
    @media (max-width: 1500px) {
        width: calc(100% / 3 - 2em);
    }
    @media (max-width: 1000px) {
        width: calc(100% / 2 - 2em);
    }
    @media (max-width: 550px) {
        width: calc(100% - 2em);
    }

    & > div.image {
        height: auto;
        position: relative;
        object-fit: cover;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: calc(100% - 20px);
        padding: 0 10px;
        max-height: 100px;
        min-height: 100px;
        margin-bottom: 20px;

        & > img {
            width: auto;
            max-height: 100%;
            max-width: 100%;
            height: auto;
            aspect-ratio: auto;
            border-radius: 3px;
        }
    }

    & > div.title {
        display: flex;
        position: relative;
        padding: 10px 20px;
        width: calc(100% - 40px);

        & > h4 {
            width: 100%;
            padding: 0;
            margin: 0;
            font-weight: 500;
            color: #2f4858;
            font-size: 1.1em;
            line-height: 1.3em;
            text-align: center;
            text-transform: uppercase;

        }
    }

    &:hover {
        box-shadow: 3px 3px 5px rgba(23, 23, 23, 0.2);
        transform: translateY(-3px);

        border: 1px solid #219653;

        & > div.title {
            & > h4 {
                color: #219653;
            }
        }
    }

`;